@import "../../../scss/variables";

.notifications {
  z-index: 10;
  position: absolute;
  right: 0;
  top: 100%;
  opacity: 1;
  visibility: visible;
  background-color: #fff;
  border: 1px solid #f2f2f2;
  box-shadow: 0px 6px 14px -6px rgba(24, 39, 75, 0.15),
  0px 10px 32px -4px rgba(24, 39, 75, 0.15);
  border-radius: $border-radius;
  width: 430px;
  margin-top: 16px;
  color: $color-black;
  transition: all 0.3s ease;
  max-height: 300px;
  overflow-y: auto;
  animation: animationVisible 0.5s ease;

  &__head {
    position: sticky;
    background-color: $color-grey;
    top: 0;
    font-size: 16px;
    padding: 16px 20px 14px 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f2f2f2;
  }

  &__title {
    display: flex;
    align-items: center;
    &__icon {
      svg {
        width: 14px;
        margin-left: 10px;
        margin-top: 2px;
        color: $color-black-05;
        transition: all 0.3s ease;

        &:hover {
          cursor: pointer;
          color: $color-black;
        }
      }
    }

    span {
      font-weight: 700;
      letter-spacing: -0.02em;
    }
  }

  &__actions {
    color: $color-black-05;
    text-decoration: underline;
    transition: all 0.3s ease 0s;

    &:hover {
      cursor: pointer;
      color: rgba(33, 33, 33, 0.7);
    }
  }

  &__info {
    &__empty {
      padding: 24px 0 18px 0;
      font-size: 16px;
      text-align: center;
    }
  }

  &__msg {
    font-size: 16px;
    line-height: 20px;
    letter-spacing: -0.02em;
    margin-bottom: 6px;
    display: inline-flex;
    justify-content: space-between;
  }

  &__text {
    display: inline-block;
    margin-right: 21px;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: -0.02em;
    color: rgba(51, 51, 51, 0.7);
  }

  &__icon {
    width: 42px;
    height: 37px;
    border-radius: $border-radius;
    background-color: $color-yellow-opacity;
    display: flex;
    align-items: center;
    justify-content: center;

    &--error {
      background-color: rgba(219, 0, 7, 0.1);
    }
  }
}

.ref {
  height: 1px;
}