@import "../../../scss/variables";

.popup {
  width: 70vw;
  height: 70vh;
}

.popupHead {
  padding: 18px 24px 14px 24px;
  background-color: $color-black-007;
  display: flex;
  align-items: center;
  justify-content: space-between;

  svg {
    cursor: pointer;
  }
}

.title {
  font-weight: bold;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  color: $color-black;
}

.block {
  position: relative;
  padding: 24px;
  // border-bottom: 1px solid #f2f2f2;
  max-height: 70vh;
  overflow-y: auto;

  > span {
    font-weight: 600;
    margin-top: 5px;
    margin-bottom: 5px;
    display: block;
    white-space: pre-wrap;
  }
}

.applyButton {
  border-radius: $border-radius;
  display: flex;
  margin-left: auto;
  margin-right: 24px;
  border: 1px solid $color-green;
  background-color: $color-green;
  color: #fff;
  padding: 5px 15px;
  margin-bottom: 20px;
}

.loader {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $color-grey;
}

.closeButton {
  svg path {
      transition: fill 0.3s ease;
    }

  &:hover {
    svg path {
      fill: $color-red;
    }
  }
}