@import "../../scss/variables";

.icon{
  svg{
    width: 16px;
    height: 16px;
  }
}

.pageHead {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 35px;
}

.pageInfo {
  display: flex;
  align-items: center;
}

.title {
  margin-right: 34px;
}

.btnIcon {
  svg {
    width: 16px;
    height: 16px;
  }
}

.table {
  border-spacing: 0;
  width: 100%;
  letter-spacing: -0.03em;

  tbody tr {
    height: 72px;
    transition: all 0.3s ease 0s;
  }

  th {
    text-align: center;
    padding: 13px 0px 10px 0px;
    background: rgba(51, 51, 51, 0.07);
    text-transform: uppercase;
    color: rgba(51, 51, 51, 0.5);
    margin-right: 20px;
    font-weight: 400;
    letter-spacing: 0px;
    font-size: 0.8em;
  }

  th:first-child {
    text-align: left;
    padding-left: 18px;
    border-radius: 4px 0px 0px 0px;
  }

  th:last-child {
    border-right: none;
    border-radius: 0px 4px 0px 0px;
  }

  td {
    vertical-align: middle;
    line-height: 18px;
    letter-spacing: -0.03em;
    padding: 11px 0px 8px 0px;
    text-align: center;
    border-top: 1px solid #f2f2f2;
    border-bottom: 1px solid #f2f2f2;
    background: #fff;

    &:first-child {
      width: 300px;
      text-align: left;
      padding: 11px 0 6px 18px;
    }

    &:last-child {
      border-right: 1px solid #f2f2f2;
    }
  }
  .btnMore {
    margin: 0px 16px 0px auto;
  }

  td:first-child {
    border-left: 1px solid #f2f2f2;
    border-right: none;
  }
}

.text {
  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.03em;
  color: $color-black-05;
}

.checkbox {
  span {
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: $color-black;
  }
}

th.role {
  text-align: center;
}

td.dropdown {
  text-align: center;
  transition: all 0.3s ease;
  cursor: pointer;

  svg path {
    transition: fill 0.3s ease;
  }

  &:hover {
    color: $color-green;

    svg path {
      fill: $color-green;
    }
  }
}

.dropdown {
  position: relative;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: -0.03em;
  svg {
    margin-left: 8px;
  }
}

.wrap {
  z-index: 5;
  position: absolute;
  top: 60px;
  left: 0px;
  text-align: initial;
  background-color: #ffffff;
  border: 1px solid #f2f2f2;
  border-radius: $border-radius;

  box-shadow: 0px 6px 18px -6px rgba(24, 39, 75, 0.15),
  0px 10px 32px -4px rgba(24, 39, 75, 0.15);

  max-width: 280px;
  max-height: 400px;
  overflow-y: auto;
  animation: animationVisible 0.5s ease;
}

.head {
  font-size: 16px;
  line-height: 20px;
  letter-spacing: -0.02em;
  font-weight: 700;
  padding: 18px 20px 16px 20px;
  border-bottom: 1px solid #f2f2f2;
  color: $color-black;
  position: sticky;
  top: 0;
  background-color: #fff;
}

.item {
  padding: 10px 15px;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: -0.03em;
  color: $color-black;
  border-bottom: 1px solid #f2f2f2;
  transition: all 0.3s ease;

  &:hover {
    background-color: rgba(51, 51, 51, 0.05);
  }

  &__selected {
    color: $color-green;
  }
}

.itemButtons {
  display: flex;
  align-items: center;
  position: sticky;
  bottom: 0;
  background-color: #fff;
  justify-content: space-between;
  border-top: 1px solid #f2f2f2;
  padding: 5px;

  button {
    padding: 5px 16px;
    font-size: 14px;
  }
}

.loader {
  padding: 10px 0;
  text-align: center;
  font-size: 16px;
}

.link {
  transition: all 0.3s ease;

  &:hover {
    color: $color-green;
    text-decoration: underline;
  }
}
