@import "../../scss/variables";
@import "../../components/tables/TableHead/table-head.module.scss";

.pageHead {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 35px;
}

.pageInfo {
  display: flex;
  align-items: center;
}

.title {
  margin-right: 34px;
  @media(min-width: 2120px){
    font-size: 28px;
  }
}

.table {
  border-spacing: 0 8px;
  width: 100%;
  margin-bottom: 30px;
}

.btnIcon {
  svg {
    width: 16px;
    height: 16px;
  }
}

.btnBorder {
  color: $color-green;
  background-color: transparent;
  border: 1px solid $color-green;
  margin-right: 16px;
}

.button {
  display: flex;
  align-items: center;
  button {
    margin-left: 16px;
  }
}

.table {
  position: relative;
  border-spacing: 0;
  width: 100%;
  letter-spacing: -0.03em;

  tbody tr {
    height: 72px;
    cursor: pointer;

    &:hover {
      td {
        transition: all 0.3s ease 0s;
        background-color: $color-grey;
      }
    }
  }

  th {
    text-align: center;
    padding: 13px 0px 10px 0px;
    background: rgba(51, 51, 51, 0.07);
    text-transform: uppercase;
    color: rgba(51, 51, 51, 0.5);
    margin-right: 20px;
    font-weight: 400;
    font-size: 14px;
    @media(min-width: 2120px){
      font-size: 22px;
      padding: 26px 0px 20px 0px;
    }

    &:first-child {
      text-align: center;
      padding-left: 18px;
      border-radius: 4px 0px 0px 0px;
      width: 25px;
    }

    &:nth-child(2), &:nth-child(5){
      width: 200px;
    }

    &:nth-child(3), &:nth-child(4){
      width: 100px;
    }

    &:last-child {
      border-right: none;
      border-radius: 0px 4px 0px 0px;
      width: 50px;
    }

    svg {
      transition: opacity 0.3s ease 0s, transform 0.15s ease 0s;
      opacity: 0;
      margin-left: 2px;
      color: #333;
    }

    .invertedSvg {
      transform: rotate(180deg);
    }

    @media screen and (max-width: 1140px) {
      vertical-align: top;
    }

    &:hover {
      color: #333;
      -webkit-text-stroke: 0.7px #333;

      svg {
        opacity: 1;
      }
    }

    &::after {
      display: block;
      content: attr(data-text);
      font-weight: 700;
      height: 0;
      overflow: hidden;
      visibility: hidden;
    }
  }

  .selectedSort {
    color: #333;
    -webkit-text-stroke: 0.7px #333;

    svg {
      opacity: 1;
    }
  }

  td {
    vertical-align: middle;
    line-height: 18px;
    letter-spacing: -0.03em;
    padding: 11px 0px 8px 0px;
    text-align: center;
    border-bottom: 1px solid #f2f2f2;
    background: #fff;
    color: $color-black-05;
    text-overflow: ellipsis;
    max-width: 150px;
    width: 30%;
    overflow: hidden;
    white-space: nowrap;
    font-size: 14px;
    @media(min-width: 2120px){
      font-size: 22px;
      padding: 22px 0px 16px 0px;
    }
    &:first-child {
      text-align: center;
      padding-left: 0;
    }

    &:last-child {
      border-right: 1px solid #f2f2f2;
    }
  }
  .btnMore {
    margin: 0 16px 0 auto;
  }

  td:first-child {
    border-left: 1px solid #f2f2f2;
    border-right: none;
  }
}

.logs_type_name {
  text-align: center;
  margin: 20px 0px 20px 0px;
  color: $color-black-05;
  font-weight: 700;
  padding-bottom: 8px;
  border-bottom: 1px solid rgba(51, 51, 51, 0.2);
  font-size: 18px;
  @media(min-width: 2120px){
    font-size: 26px;
  }
}

.checkbox {
  span {
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: $color-black;
  }
}

.all_logs_table {
  td,
  th {
    width: 40px;
  }
}

td.fail {
  color: $color-red;
  font-weight: 700;
  cursor: pointer;
}

td.success {
  color: $color-green;
  font-weight: 700;
  cursor: pointer;
}

.scriptLogsLoader {
  display: block;
  text-align: center;

  &__search {
    div {
      position: absolute;
      top: 35px;
      left: calc(50% - 40px);
    }
    right: 0;
    top: 0;
    position: absolute;
    z-index: 5;
    background-color: rgba(117, 117, 117, 0.2);
    width: 100%;
    height: 100%;
  }
}