@import "../../../scss/variables";

.popup {
  background-color: #fff;
  width: 334px;
}

.popupHead {
  padding: 18px 24px 14px 24px;
  background-color: $color-black-007;
  display: flex;
  align-items: center;
  justify-content: space-between;

  svg {
    cursor: pointer;

    path {
      transition: fill 0.3s ease;
    }

    &:hover {
      path {
        fill: $color-red;
      }
    }
  }
}

.title {
  font-weight: bold;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  color: $color-black;
}

.block {
  padding: 24px;
  // border-bottom: 1px solid #f2f2f2;
  max-height: 80vh;
  overflow-y: auto;
}

.date {
  display: flex;
  align-items: center;

  input {
    background: #ffffff;
    border: 1px solid #f2f2f2;
    border-radius: 4px;
    font-size: 14px;
    line-height: 18px;
    color: $color-black-05;
    padding: 10px 12px 7px 12px;
    max-width: 116px;
    margin-right: 8px;
  }
}

.dateButton {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 42px;
  height: 37px;
  background-color: #fff;
  border: 1px solid #f2f2f2;
  border-radius: 4px;
  color: $color-black;
}

.applyButton {
  text-align: right;

  button {
    margin: 0 24px 10px auto;
  }
}

.category {
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: -0.02em;
  color: $color-black;
  margin-bottom: 14px;
}

.checkbox {
  margin-bottom: 6px;

  span {
    font-size: 14px;
    line-height: 18px;
  }

  span::before {
    margin-right: 12px;
  }
}

.popupButton {
  margin-top: 44px;

  button {
    margin-left: auto;
  }
}

.loader {
  padding: 10px 0;
  text-align: center;
}