@import "../../../../scss/variables";

.title {
  margin-bottom: 35px;
  font-weight: bold;
  font-size: 21px;
  @media(min-width: 2120px){
    font-size: 30px;
  }
  line-height: 27px;
  letter-spacing: -0.01em;
  color: $color-black;
}

.popup {
  display: flex;
  position: absolute;
  bottom: 20px;
  right: 20px;
  transition: 0.5s;
  background-color: #a9c23f;
  color: #fff;
  padding: 20px;
  border-radius: 8px;
}

.closed {
  display: flex;
  position: absolute;
  bottom: -50px;
  right: 20px;
}

.buttons {
  display: flex;
  align-items: center;
  button{
    margin-left: 7px;
  }
}

.borderGreen {
  background-color: #fff;
  border: 1px solid $color-green;
  color: #a9c23f;
  margin-right: 16px;
}

.yellow {
  background-color: $color-yellow;
  color: #fff;
  margin-right: 16px;
}

.type {
  margin-right: 8px;
}
