@import "../../../scss/variables";

.popup {
  background-color: #fff;
  width: 334px;
}

.popupHead {
  padding: 18px 24px 14px 24px;
  background-color: $color-black-007;
  display: flex;
  align-items: center;
  justify-content: space-between;
  svg {
    cursor: pointer;
    path {
      transition: fill 0.3s ease;
    }

    &:hover path {
      fill: $color-red;
    }
  }
}

.title {
  font-weight: bold;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  color: $color-black;
}

.block {
  padding: 24px;
  // border-bottom: 1px solid #f2f2f2;
  max-height: 80vh;
  overflow-y: auto;
  > span {
    font-weight: 600;
    margin-top: 5px;
    margin-bottom: 5px;
    display: block;
    white-space: pre-wrap;
  }
}

.applyButton {
  button {
    margin-left: auto;
    margin-right: 24px;
    margin-bottom: 15px;
    align-self: flex-end;
  }
}
