@import "../../scss/variables";

.text {
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: -0.01em;
  color: $color-black;

  span {
    font-weight: 400;
  }
}

.buttons {
  display: flex;
  max-width: 100px;
}

.date {
  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.03em;
  color: $color-black;
  opacity: 0.5;
}

.period {
  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.03em;
  color: $color-black;
  opacity: 0.5;
}

.loader {
  width: 40px;
  text-align: center;
}